import React from "react";
import {
  Wrapper,
  Header,
  StyledForm,
  SubHeader,
  StyledSpan,
  InlineWrapper,
} from "./Login.style";
import FormField from "components/molecules/FormField/FormField";
import Button from "components/atoms/Button/Button";
import SocialButton from "components/atoms/SocialButton/SocialButton";
import StyledLink from "components/atoms/StyledLink/StyledLink";
import { useForm, SubmitHandler } from "react-hook-form";
import { authActions } from "slices/authSlice";
import { useAppDispatch, useAppSelector } from "store/hooks";
import { getLoadingState } from "slices/apiCallSlice";
import { SIGNUP } from "constants/routes";
import { useNavigate } from "react-router-dom";

type InputsTypes = { login: string; password: string };

const Login = () => {
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const isLoading = useAppSelector(getLoadingState);
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm<InputsTypes>();

  const onSubmit: SubmitHandler<InputsTypes> = (data) => {
    const userData = {
      email: data.login,
      password: data.password,
      callback: navigate,
    };

    dispatch(authActions.login(userData));
  };

  return (
    <Wrapper>
      <Header>Zaloguj si</Header>
      <SubHeader>Hej, podaj swoje dane eby si zalogowa</SubHeader>
      <StyledForm onSubmit={handleSubmit(onSubmit)}>
        <FormField
          {...register("login", { required: "To pole jest wymagane" })}
          id="login"
          name="login"
          label="Nazwa uytkownika"
          isError={!!errors.login}
          errorMessage={errors?.login?.message}
        />
        <FormField
          {...register("password", { required: "To pole jest wymagane" })}
          id="password"
          type="password"
          name="password"
          label="Haso"
          isError={!!errors.password}
          errorMessage={errors?.password?.message}
        />
        <Button disabled={isLoading}>Zaloguj si</Button>
      </StyledForm>
      <StyledSpan useLines>Albo zaloguj si z</StyledSpan>
      <InlineWrapper>
        <SocialButton
          isGoogle={true}
          onClick={() => dispatch(authActions.loginWithGoogle({callback: navigate}))}
          src="https://upload.wikimedia.org/wikipedia/commons/5/53/Google_%22G%22_Logo.svg"
          alt="google icon"
        >
          Google
        </SocialButton>
        <SocialButton onClick={() => dispatch(authActions.loginWithFacebook({callback: navigate}))}>Facebook</SocialButton>
      </InlineWrapper>
      <StyledSpan>
        Nie masz jeszcze konta?{" "}
        <StyledLink to={SIGNUP}>Zarejestruj si</StyledLink>
      </StyledSpan>
    </Wrapper>
  );
};

export default Login;
